.button{
  border: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.10);
  height: 58px;
  width: 58px;
  font-weight: normal;
  font-size: 20px;
  margin: 16px auto;
  padding: 3px;
  transition: all 0.2s ease-in;
  cursor: pointer;
  background: white;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button svg{
  transition: fill 0.2s ease-in;
}

.button .button-inner{
  display: flex;
  justify-content: center;
  align-items: center;
}

.button.primary{
  background: #0984E3;
  box-shadow: 0 3px 6px rgba(9, 132, 227, .30);
  color: white;
}

.button.value{border-radius: 6px; width: 100%;}
.button:not(.value){border-radius: 100%;}
.button-icon{background-size: cover;}
.button.no-back{background: none; box-shadow: none; color: #777777;}
.button:active, .button:focus{outline: none;}

.button-icon{
  width: 100%;
  height: 100%;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border: 3px solid white;

  display: inline-block;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 100%;
}

.button:hover{
  outline: none;
  box-shadow: 0 6px 6px rgba(0, 0, 0, .10);
  transform: translateY(-3px) scale(1.01);
}

.button.primary:hover{
  box-shadow: 0 6px 6px rgba(9, 132, 227, .30);
}

.button.no-back, .button.no-back:hover, .button.no-back:focus, .button.no-back:active{
  box-shadow: none;
  transform: none;
  color: white;
}

.button.no-back:active{color: #0984E3;}

.button.profile{
  margin: 0;
  padding: 0;
  line-height: 0;
  border: 3px solid #0984E3;
}

.button span{
  margin-left: 16px;
}

.button.success{
  background-color: #71CB7F;
  box-shadow: 0 3px 6px rgba(113, 203, 127, .30);
}
.button.success:hover{
  box-shadow: 0 3px 6px rgba(113, 203, 127, .30);
}
